<template>
  <div class="add">
    <div class="nav">
      <div class="left" @click="back">&times;</div>
      <div class="right">
        <div class="date-box">
          <div class="week">星期{{week}}</div>
          <div class="date">{{Month}}/{{Year}}</div>
        </div>
        <div class="time">{{Day}}</div>
      </div>
    </div>
    <div class="content">
      <div class="img-box">
        <img class="auto-img" src="../assets/guanzhu/tushetou.png" alt="">
      </div>
      <div class="title">每一顿饭都值得分享</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Add',
    data() {
      return {
        Year: '',
        Month: '',
        Day: '',
        week: '',
      }
    },
    created() {
      this.getDate();
    },
    methods: {
      // 获取时间
      getDate() {
        let date = new Date()
        
        this.Year = date.getFullYear();
        this.Month = date.getMonth() + 1;
        this.Month = this.Month >= 10 ? this.Month : '0' + this.Month; 
        this.Day = date.getDate();
        this.Day = this.Day >= 10 ? this.Day : '0' + this.Day;
        this.week = date.getDay();
        let week = ['日', '一', '二', '三', '四', '五', '六'];
        this.week = week[this.week];
        
      },
      // 返回
      back() {
        this.$router.go(-1);
      }
    }
  }
</script>

<style lang="less" scoped>
  .add {
    .nav {
      padding: 10px;
      display: flex;
      .left {
        font-size: 36px;
      }
      .right {
        margin-left: auto;
        display: flex;
        align-items: center;
        font-weight: bold;
        .time {
          margin-left: 5px;
          font-size: 36px;
        }
      }
    }
    .content {
      margin-top: 150px;
      text-align: center;
      color: #444;
      .img-box {
        width: 50px;
        height: 50px;
        margin-left: calc(50% - 25px);
      }
    }
  }
</style>
